import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import SliderMenu from 'react-slider-menu';
import Dropdown from 'react-bootstrap/Dropdown'

class Header extends Component {
    render(){
        return (
            <header>
                <div id="desktop">
                    <div className="logo">
                        <a href="/longbirds"><img src={require('../../imgs/logos/longBirdsLogo.png')}/></a>
                    </div>
                    <nav>
                        <ul>
                            <li>
                                <Link to="/Music">Music</Link>
                            </li>

                            <li>
                                <Link to="/Products">Shop</Link>
                            </li>

                            <li>
                                <Link to="/Videos">Videos</Link>
                            </li>

                            <li>
                                <Link to="/ShowDates">Show Dates</Link>
                            </li>

                            <li className="last">
                                <Link to="/Contact">Contact</Link>
                            </li>
                        </ul>
                    </nav>
                </div>

                <div id="mobile">
                    <div className="logo">
                        <a href="/longbirds"><img src={require('../../imgs/logos/longBirdsLogo.png')}/></a>
                    </div>
                    <div>
                        <center style={{padding: "5%"}}>
                            <Dropdown>
                                <Dropdown.Toggle variant="info" size="sm" id="dropdown-basic">
                                    MENU
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="/Music">Music</Dropdown.Item>
                                    <Dropdown.Item href="/Products">Shop</Dropdown.Item>
                                    <Dropdown.Item href="/Videos">Videos</Dropdown.Item>
                                    <Dropdown.Item href="/ShowDates">Show Dates</Dropdown.Item>
                                    <Dropdown.Item href="/Contact">Contact</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </center>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header;
